export const getfootballteams = (page) => {
    return async (dispatch) => {
      console.log(page)
        // dispatch(getteamsdatainit())
      await http.get(`/football/team/list?page=${page}`).then((response) => {
        dispatch({
          type: 'GET_FOOTBALL_TEAMS',
          data: response.data.data
        })
        console.log(response)
      })
    }
  }

