// ** React Imports
import { Fragment, useState, useEffect } from 'react'
// ** Invoice List Sidebar
import UiLoader from '@components/ui-loader'
import { useForm } from 'react-hook-form'
import classnames from 'classnames'
// import AddQuiz from '../add'
import BreadCrumbs from '@components/breadcrumbs'
// ** Store & Actions
import {clearData, getcricketteams, getcricketteamseries, getcricketSeries, cricketteamsviwe, getcricketTotal, collectcricketseriesid} from '../../store/actions'
import { useSelector, useDispatch } from 'react-redux'
import { selectThemeColors } from '@utils'
// import { selectThemeColors } from '@utils'
import { useHistory } from 'react-router-dom'
// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { ChevronDown, Edit, Filter } from 'react-feather'
import DataTable from 'react-data-table-component'
//import UiLoader from '@components/ui-loader'
import { Card, Row, Col, Label, Spinner, Button } from 'reactstrap'

// ** Stylesy
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import AppCollapse from '@components/app-collapse'

// ** Import Scrollbar Paginate Component
import { AsyncPaginate } from "react-select-async-paginate"

const CustomHeader = ({store, setListData, setCurrentPage}) => {

  const defaultValues = {
    reactSelect: null
  }

  const store1 = useSelector((state) => state.teams)

  const { handleSubmit } = useForm({ defaultValues })
  const [val, setVal] = useState(false)
  const [serilist, setSerilist] = useState("")
  const [error, setError] = useState(false)
  const [page, setPage] = useState(1)
  const [value] = useState(null)
  const dispatch = useDispatch()


  const serieslist = (e) => {
    setVal(false)
    console.log(error)
    setError(false)
    setSerilist(e?.value)
    
  }
console.log(serilist)
  const submitseries = (e) => {
    setVal(true)
     e.preventDefault()
     setCurrentPage(1)
     if (serilist === "") {
       setError(true)
    } else {
      dispatch(collectcricketseriesid(serilist))
    }
    dispatch(getcricketteamseries(1, serilist))       
     setListData(store.cricket_teams_series)
     
  }

  useEffect(() => {
    dispatch(getcricketTotal(page))
  }, [page])

  useEffect(() => {
    // console.log('Component Mounted')
    return () => {
      // console.log('component Unmounted')
      dispatch(clearData())
    }
  }, [])
  
  useEffect(() => {

      if (store.cricket_teams_series.length !== 0) {
        setListData(store.cricket_teams_series)
      } else {
        if (store.cricket_teams_series.length === 0 && val) {
          setListData(store.cricket_teams_series)
        } else {
          setListData(store.cricket_teams)          
        }
      }
  }, [submitseries])

  const handleKeyPress = (e) => {
    if (e.keyCode === 13) {
      setCurrentPage(1)
      dispatch(collectcricketseriesid(serilist))
      dispatch(getcricketteamseries(1, serilist))   
    }
  }

  // ************************************************************************************* //

  const options = []
  for (let i = 0; i < store1.cricket_total?.data?.total; i++) {
    if (page === store1.cricket_total?.data?.current_page) {
      options.push({
        value: `${store1.cricket_total?.data?.data[i]?.id}`,
        label: `${store1.cricket_total?.data?.data[i]?.series_name}`
      })
    }
    
  }
  const sleep = ms => new Promise(resolve => { setTimeout(() => { resolve() }, ms) })
  const optionsPerPage = 10
  
  const loadOptions = async (search, prevOptions, page) => {
    await sleep(1000)
    if (page === store1.cricket_total?.data?.current_page) {
      const nextData = store1.cricket_total?.data?.data?.map((nextData) => { return { value: nextData.id, label: nextData.series_name } })
      const scrollLength = [...prevOptions, ...nextData]
      let filteredOptions
      if (!search) {
        filteredOptions = scrollLength.length > 10 ? scrollLength : options
      } else {
        const searchLower = search.toLowerCase()
        filteredOptions = [...scrollLength.length > 10 ? scrollLength : options].filter(({ label }) => label.toLowerCase().includes(searchLower)
        )
      }
  
      const hasMore = Math.ceil(store1.cricket_total?.data?.total / optionsPerPage) > page
      const slicedOptions = filteredOptions.slice(
        (page - 1) * optionsPerPage,
        page * optionsPerPage
      )
  
      return {
        options: slicedOptions,
        hasMore
      }
    }
  }
  const defaultAdditional = {
    page: 1
  }
  
  const loadPageOptions = async (q, prevOptions, { page }) => {
    setPage(page)
    const { options, hasMore } = await loadOptions(q, prevOptions, page)
    return {
      options:[...options],
      hasMore,
  
      additional: {
        page: page + 1
      }
    }
  }
  
   // ************************************************************************************* //


  const data = [
    {
      title: <div><Filter size={18}/> Filter...</div>,
      content: (
        <>
        <Row>
        <Col className='mb-1' md='4' sm='12'>
          <Label>series</Label>
              <AsyncPaginate
               menuPortalTarget={document.querySelector('body')}
                theme={selectThemeColors}
                className={classnames('react-select', { 'is-invalid': serilist === "" && val })}
                classNamePrefix='select'
                additional={defaultAdditional}
                value={value?.label}
                loadOptions={loadPageOptions}
                onChange={(e) => serieslist(e)}
                onKeyDown={(e) => handleKeyPress(e)}
              />
          {serilist === "" && val ?  <h5 className='text-danger'>Please enter a value</h5> : ""}
          </Col>
        </Row>
        <Button.Ripple 
        color='primary'
        onClick={(e) => handleSubmit(submitseries(e))}
         >submit</Button.Ripple>
         </>
      )
    }
  ]
 
    return (
    <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-2 mb-75">
      <AppCollapse data={data} type='border' accordion className='mb-1' />
    </div>
  )
}

const UsersList = () => {
  
  const store = useSelector((state) => state.teams)

  // ** Store Vars
  const dispatch = useDispatch()

  // ** States
  const [searchTerm, setSearchTerm] = useState('')
  const [listData, setListData] = useState([])
  const [currentPage, setCurrentPage] = useState(1)
  const [paginate, setPaginate] = useState(0)
  
  const history = useHistory()
  console.log(store)
 
  useEffect(() => {
    if (store.cricket_teams_series.length !== 0) {
      dispatch(getcricketteamseries(currentPage, store.seriescricketid))
    } else {
      
      dispatch(getcricketteams(currentPage))
    }
    dispatch(getcricketSeries())
  }, [paginate])

  useEffect(() => {
    if (store.isloading) {
      <Spinner/>
    } else {
      // console.log("alldata")
      setListData(store.cricket_teams)
    }
  }, [store.isloading])

  // ** Function in get data on page change
  const handlePagination = (page) => {
    setPaginate(paginate + 1)
    setCurrentPage(page.selected + 1)
  }

  // ** Function in get data on rows per pag
  
  const editHandler = (row) => {
    // console.log(row.size)
    history.push({pathname:"/cricketteams/edit"})
    dispatch(cricketteamsviwe(row.id))
    
  }

  const columns = [
    {
      name: '#',
      minWidth: '20px',
      sortable: true,
      cell: (row, pos) =>  `#${store?.cricket_page.from + pos }`
    },
    {
      name: 'Team Logo',
      minWidth: '10px',
      selector: 'name',
      sortable: true,
      cell: (row) => (
        <img src={row.path} style={{ height: '40px', width: '40px'}} />
      )
    },
    {
      name: 'Team Name',
      minWidth: '10px',
      selector: 'type',
      sortable: true,
      cell: (row) => (row.team_name) 
    },
    {
      name: 'Team Short Name',
      minWidth: '160px',
      selector: 'invite_id',
      sortable: true,
      cell: (row) => row.team_sn
    },
    {
      name: 'Active',
      minWidth: '138px',
      selector: 'active',
      sortable: true,
      cell: (row) => (
        <div className="column-action">
          {/* <a href={`/walls/edit/${row.feed_id}`}> */}
          <Edit width="17" className="cursor-pointer" title="Edit" onClick={() => editHandler(row)} />
          {/* </a> */}
         
        </div>
      )
    }   
  ]

  // ** Function in get data on search query change
  const handleFilter = (val) => {
    setSearchTerm(val)
  }
  // console.log("list", listData)
  useEffect(() => {
   if (searchTerm) {
      const filterList = store.cricket_teams.filter((userItem) => {
        
        const newserch =
        userItem.id.toString().toLowerCase().startsWith(searchTerm.toLowerCase()) || 
        userItem.prize_rank.toString().toLowerCase().startsWith(searchTerm.toLowerCase()) ||
        userItem.contest_name?.toLowerCase().startsWith(searchTerm.toLowerCase()) ||
        userItem.contest_prize_type.toLowerCase().startsWith(searchTerm.toLowerCase()) ||
        userItem.contest_prize_caption.toLowerCase().startsWith(searchTerm.toLowerCase()) ||
        userItem.contest_prize_value?.toString().toLowerCase().startsWith(searchTerm.toLowerCase())
       
       
       const includes = 
       userItem.id.toString().toLowerCase().startsWith(searchTerm.toLowerCase()) ||
       userItem.prize_rank.toString().toLowerCase().startsWith(searchTerm.toLowerCase()) ||
       userItem.contest_name?.toLowerCase().startsWith(searchTerm.toLowerCase()) ||
       userItem.contest_prize_type.toLowerCase().startsWith(searchTerm.toLowerCase()) ||
       userItem.contest_prize_caption.toLowerCase().startsWith(searchTerm.toLowerCase()) ||
       userItem.contest_prize_value?.toString().toLowerCase().startsWith(searchTerm.toLowerCase())
       
    if (newserch) {
        return newserch
      } else if (!newserch && includes) {
        return includes
      } else return null

      })

      setListData(filterList)
      setCurrentPage(1)
    } else {
      setListData(store.cricket_teams)
    }
  }, [searchTerm])

  if (store.isloading) {
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader
          blocking
        />
      </div>
    )
  }

  // console.log(store.cricket_page.last_page)
  // ** Custom Pagination 
  const CustomPagination = () => {
    const count = store.cricket_page.last_page

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={
          'pagination react-paginate justify-content-end my-2 pr-1'
        }
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })
    if (listData && isFiltered) {
      return []
    } else {
      return listData
    }
  }

  return (
    <Fragment>
        <BreadCrumbs breadCrumbTitle='Cricket Teams' breadCrumbActive='List' />
      <Card>
        <DataTable
          noHeader
          pagination
          subHeader
          responsive
          paginationServer
          columns={columns}
          paginationPerPage
          paginationDefaultPage={currentPage}
          sortIcon={<ChevronDown />}
          className="react-dataTable"
          paginationComponent={CustomPagination}
          data={dataToRender()}
          subHeaderComponent={
            <CustomHeader
              handlePerPage
              rowsPerPage
              searchTerm={searchTerm}
              handleFilter={handleFilter}
              store={store}
              setListData={setListData}
              listdata={listData}
              setCurrentPage={setCurrentPage}
              currentPage={currentPage}
            />
          }
        />
      </Card>
    </Fragment>
  )
}

export default UsersList
